/*-------------------公用CSS3动画------------------*/
@charset "UTF-8";
/*--s--*/
@-webkit-keyframes slideFadeOutRight {
from {
        opacity: 0;
        -webkit-transform: translateX(-100%);
        transform: translateX(-100%);
}
to {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
}
}
@keyframes slideFadeOutRight {
from {
        opacity: 0;
        -webkit-transform: translateX(100%);
        transform: translateX(100%);
}
to {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
}
}
.slideFadeOutRight{
    -webkit-animation: slideFadeOutRight 1s both;
    animation: slideFadeOutRight 1s both;
}
/*--f--*/
/*--s--*/
@-webkit-keyframes slideFadeToRight {
from {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
}
to {
        opacity: 0;
        -webkit-transform: translateX(100%);
        transform: translateX(100%);
}
}
@keyframes slideFadeToRight {
from {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0);
}
to {
        opacity: 0;
        -webkit-transform: translateX(100%);
        transform: translateX(100%);
}
}
.slideFadeToRight{
    -webkit-animation: slideFadeToRight 1s both;
    animation: slideFadeToRight 1s both;
}
/*--f--*/
/*--s--*/
@-webkit-keyframes clockwiseRotate {
from {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
to {
        -webkit-transform: translateY(11.5px) rotate(45deg);
        transform: translateY(11.5px) rotate(45deg);
}
}
@keyframes clockwiseRotate {
from {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
to {
        -webkit-transform: translateY(11.5px) rotate(45deg);
        transform: translateY(11.5px) rotate(45deg);
}
}
@-webkit-keyframes anticlockwiseRotate {
from {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
to {
        -webkit-transform: translateY(-11.5px) rotate(-45deg);
        transform: translateY(-11.5px) rotate(-45deg);
}
}
@keyframes anticlockwiseRotate {
from {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
to {
        -webkit-transform: translateY(-11.5px) rotate(-45deg);
        transform: translateY(-11.5px) rotate(-45deg);
}
}
/*r*/
@-webkit-keyframes clockwiseRotatereverse {
from {
        -webkit-transform: translateY(11.5px) rotate(45deg);
        transform: translateY(11.5px) rotate(45deg);
}
to {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
}
@keyframes clockwiseRotatereverse {
from {
        -webkit-transform: translateY(11.5px) rotate(45deg);
        transform: translateY(11.5px) rotate(45deg);
}
to {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
}
@-webkit-keyframes anticlockwiseRotatereverse {
from {
        -webkit-transform: translateY(-11.5px) rotate(-45deg);
        transform: translateY(-11.5px) rotate(-45deg);
}
to {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
}
@keyframes anticlockwiseRotatereverse {
from {
        -webkit-transform: translateY(-11.5px) rotate(-45deg);
        transform: translateY(-11.5px) rotate(-45deg);
}
to {
        -webkit-transform: translateY(0) rotate(0deg);
        transform: translateY(0) rotate(0deg);
}
}
.clockwiseRotate{
    animation: clockwiseRotate 1s both;
    -webkit-animation: clockwiseRotate 1s both;
}
.clockwiseRotatereverse{
    animation: clockwiseRotatereverse 1s both;
    -webkit-animation: clockwiseRotatereverse 1s both;
}
.anticlockwiseRotate{
    animation: anticlockwiseRotate 1s both;
    -webkit-animation: anticlockwiseRotate 1s both;
}
.anticlockwiseRotatereverse{
    animation: anticlockwiseRotatereverse 1s both;
    -webkit-animation: anticlockwiseRotatereverse 1s both;
}
/*--f--*/
/*--s--*/
@-webkit-keyframes fadeFromBottom {
from {
        opacity: 0;
        -webkit-transform: translateY(50px);
        transform: translateY(50px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
}
}
@keyframes fadeFromBottom {
from {
        opacity: 0;
        -webkit-transform: translateY(50px);
        transform: translateY(50px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
}
}
.fadeFromBottom{
    -webkit-animation: fadeFromBottom 1s both;
    animation: fadeFromBottom 1s both;
}
/*--f--*/
/*--s--*/
@-webkit-keyframes scaleOut {
from {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
}
to {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
}
}
@keyframes scaleOut {
from {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
}
to {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
}
}
.scaleOut{
    -webkit-animation: scaleOut 1s both;
    animation: scaleOut 1s both;
}
@-webkit-keyframes scaleIn {
from {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
}
to {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
}
}
@keyframes scaleIn {
from {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
}
to {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
}
}
.scaleIn{
    -webkit-animation: scaleIn 1s both;
    animation: scaleIn 1s both;
}
